import React from 'react';
import footer from '../Images/footer-img.jpg'
import fb from '../Images/fbb.png'
import linkedIn from '../Images/linked.png'
import twitter from '../Images/social-twitter.png'
import git from '../Images/git.png'
import axios from "axios";
import { ToastContainer,toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const Footer: React.FC<any> = (props) => {
    const ValidateEmail=(mail:any) =>
{
 if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail))
  {
    return (true)
  }
   
    return (false)
}
    const sendMail = () => {
        const name:any = document.getElementById("name");
        const email:any = document.getElementById("email");
        const textArea:any = document.getElementById("textArea");
     const data=   {
	
            "toList":["rishavmittal2903@gmail.com"],
            "ccList":[email.value],
            "subject":"Developer position",
            "name":name.value,
            "text":`${textArea.value}`
        }
        if (!name.value) {
            let nameElem: any = document.getElementById("name");
            toast.error("Name is mandatory field")
            nameElem.focus();
        }
        else if (!email.value) {

            let emailElem: any = document.getElementById("email");
            toast.error("Email is mandatory field")
            emailElem.focus();
        }
        else if(!ValidateEmail(email.value))
        {
            let emailElem: any = document.getElementById("email");
            toast.error("Email format is incorrect")
            emailElem.focus();
        }
        else if(!textArea.value)
        {
            let textAreaElem: any = document.getElementById("textArea");
            toast.error("Description is mandatory");
            textAreaElem.focus();
        }
        else {
            axios({
                method: "POST",
                url: "https://sendemailgateway.herokuapp.com/sendNotification/sendMail",
                data:data,
                headers: {
                  'Access-Control-Allow-Origin': '*',
                  'Content-Type': 'application/json'
                }
                })
                .then( (res)=> {
                    toast.success("Email sent successfully.")
                })
                .catch( (err) =>{
                  
                });
        }
    }
    return (
        <footer className="site-footer">
            <ToastContainer
        autoClose={3000}
        position="top-center"
        className="toast-container"
        toastClassName="dark-toast"
      />
            <div className="site-container">
                <div className="footer-inner">
                    <div className="footer-info">
                        <div className="footer-info__left">
                            <img src={footer} alt="about me image" />
                            <a href="https://www.linkedin.com/in/rishav-mittal-aa5b95134/" className="txtDecoration"><p>Read more about me</p></a>
                        </div>
                        <div className="footer-info__right">
                            <h5>Get In Touch</h5>
                            <p className="footer-phone">Phone: +91-9206134708</p>
                            <p>Email : rishavmittal2903@gmail.com</p>
                            <div className="social-icons">
                                <a href="https://twitter.com/RishavM25858663" target="_blank"><img src={twitter} alt="social icon" /></a>
                                <a href="https://www.facebook.com/rishav.mittal.3/" target="_blank"><img src={fb} alt="social icon" /></a>
                                <a href="https://github.com/rishavmittal2903" target="_blank"><img src={git} alt="social icon" /></a>
                                <a href="https://www.linkedin.com/in/rishav-mittal-aa5b95134/" target="_blank"><img src={linkedIn} alt="social icon" /></a>
                            </div>
                        </div>
                    </div>
                    <div className="footer-contact-form">
                        <h5>Contact Form</h5>
                        <form className="contact-form">
                            <div className="contact-form__input">
                                <input type="text" id="name" name="name" placeholder="Name" />
                                <input type="email" id="email" name="email" placeholder="Email" />
                            </div>
                            <textarea id="textArea"></textarea>
                            <input type="button" name="submit" value="Send Mail" onClick={() => sendMail()} className="submit-button" />
                        </form>
                    </div>
                </div>
            </div>
            <div className="footer-bottom">
                <div className="site-container footer-bottom-inner">
                    <p>Author | Rishav Mittal | No rights Reserved | Feel free to take Reference</p>

                </div>
            </div>
        </footer>


    );
}
export default Footer;
